<template>
  <main>
    <template v-if="openAvgWeight">

      <el-tabs type="border-card" v-model="activeName" @tab-click="onTabClick(activeName)">
        <el-tab-pane label="" name="">
          <calculation @getStatus="getCalculStatus" @goto="goto"/>
        </el-tab-pane>
        <el-tab-pane :disabled="calculStatus" label="" name="">
        <span v-if="calculStatus" slot="label">
          <el-tooltip class="item" effect="light" content="" placement="top">
            <div></div>
          </el-tooltip>
        </span>
          <overview ref="overview" @goto="goto"/>
        </el-tab-pane>
        <el-tab-pane :disabled="calculStatus" label="" name="">
        <span v-if="calculStatus" slot="label">
          <el-tooltip class="item" effect="light" content="" placement="top">
            <div></div>
          </el-tooltip>
        </span>
          <detailed ref='detailed'/>
        </el-tab-pane>
      </el-tabs>

    </template>
    <template v-else>
      <div class="tip">!</div>
    </template>
  </main>
</template>
<script>
  import calculation from './calculation';
  import detailed from './detailed'
  import overview from './overview'

  export default {
    name: "checkInterchangeFee",
    data() {
      return {
        activeName: '',
        calculStatus: true,
        row: {}
      }
    },
    created() {
    },
    methods: {
      getCalculStatus(val, row) {
        if (row) this.row = row;
        this.calculStatus = val;
      },
      goto(row) {
        this.activeName = '';
        this.$refs.detailed.toDetailed(row);
        this.z_method.refreshSpread();
      },
      onTabClick(name) {
        if (name === '') {
          this.$refs.overview.initialization(this.row)
        } else if (name == '') {
          this.$refs.detailed.initialization(this.row);
          this.z_method.refreshSpread()
        }
      }
    },
    computed: {
      openAvgWeight() {
        return this.z_param.avgStatus
      }
    },
    components: {calculation, detailed, overview},
  }
</script>

<style scoped lang="scss">
  .tip {
    font-size: 32px;
    font-weight: bold;
    text-align: center;
    padding-top: 15%;
    color: #e35664;
  }
</style>
