// import moment from 'moment'
import {
  get,
  post,
  axiosConfig,
  postJson
} from '@/config/axios.config';
import {
  cloneDeep
} from 'lodash';
import moment from 'moment';
export const pagerMixin = {
  activated() {
    this.$nextTick(() => {
      this.z_method.refreshSpread()
    })
  },
  data() {
    return {
      // 
      pagination: {
        currentPage: 1,
        total: 0,
        pageSize: 100,
      }, // 
      optionsClone: {},
      options: {},
      // 
      btnDisabled: false, // 
      exportObj: {
        visible: false,
        sum: 1, //
        current: 0, //
        fileName: '',
        excelData: [],
      },
      // 
      requestMode: 'post',
      requestUrl: '/',
      requestPageSize: 5000,
      //  
      checklistData: [],
      isLayering: false, // 
      isQs: true, //  falsejson
      billsCode: '',
      sbc: '',
    };
  },
  mounted() {},
  methods: {
    query() {
      console.log('000000');
      // throw new Error('component must implement query method')
    },
    // 
    fetchData(val) {
      this.pagination = {
        ...this.pagination,
        pageSize: val.pageSize ? val.pageSize : this.pagination.pageSize,
        currentPage: val.page ? val.page : 1,
      };
      // if (val.page) {
      //   this.pagination.currentPage = val.page
      // }
      let options = {};
      if (JSON.stringify(this.optionsClone) == '{}') {
        this.msg('');
        return;
      }
      if (this.isLayering) {
        options = {
          ...this.optionsClone,
          pageParam: {
            pageSize: this.pagination.pageSize,
            currentPage: this.pagination.currentPage,
          },
        };
      } else {
        options = {
          ...this.optionsClone,
          pageSize: this.pagination.pageSize,
          currentPage: this.pagination.currentPage,
        };
      }
      this.query(options);
    },
    // 
    exportCsv(_this) {
      _this.exportObj.visible = true; //msg
    },
    closeDialog(val) {
      this.exportObj.visible = val;
      this.exportObj.current = 1;
    },
    searchExport() {
      this.btnDisabled = true;
      if (this.tableData.length === 0) {
        this.msg('');
        this.btnDisabled = false;
        return;
      }
      let options = cloneDeep(this.optionsClone);
      // this.searchExportIn(this.pagination.total, options);
      if (this.pagination.total > 200000) {
        this.msg('20W');
        this.btnDisabled = false;
        return;
      }
      let num = Math.ceil(this.pagination.total / this.requestPageSize);
      // 
      this.exportObj.sum = num;
      this.exportObj.current = 0;
      this.exportSync(0, num, options, this.requestUrl);
    },
    exportSync(currentPage, num, options, url) {
      if (currentPage == num) {
        this.btnDisabled = false;
        // 
        return;
      }
      ++currentPage;
      // 
      if (this.isLayering) {
        options.pageParam.pageSize = this.requestPageSize;
        options.pageParam.currentPage = currentPage;
      } else {
        options.pageSize = this.requestPageSize;
        options.currentPage = currentPage;
      }
      if (this.requestMode == 'post') {
        this.exportPost(num, currentPage, options, url);
      } else {
        this.exportGet(num, currentPage, options, url);
      }
    },
    // post
    exportPost(num, currentPage, options, url) {
      if (this.isQs) {
        post(url, options, this.isQs)
          .then(res => {
            this.exportNormal(res, num, currentPage, options, url);
          })
          .catch(res => {
            this.exportError(num);
          });
      } else {
        postJson(url, options)
          .then(res => {
            this.exportNormal(res, num, currentPage, options, url);
          })
          .catch(res => {
            this.exportError(num);
          });
      }
    },
    // get
    exportGet(num, currentPage, options, url) {
      get(url, options, this.isQs)
        .then(res => {
          this.exportNormal(res, num, currentPage, options, url);
        })
        .catch(res => {
          this.exportError(num);
        });
    },
    // 
    exportNormal(res, num, currentPage, options, url) {
      if (res.code != 0) {
        res.data.list = [];
      }
      if (this.sbc === 'sendMailFeeOrPayFeeDetail') {
        res.data.list = this.calculation(res.data.list);
      }
      if (this.sbc === 'sendReceiveFeeSummary') {
        res.data.list = this.ischeckrow(res.data.list);
      }
      this.exportObj = {
        ...this.exportObj,
        sum: num, //
        current: currentPage, //
        excelData: res.data.list,
      };
      //  
      this.exportCsv(this);
      this.exportSync(currentPage, num, options, url);
    },
    // 
    exportError(num) {
      this.btnDisabled = false;
      this.msg('');
      this.exportObj = {
        ...this.exportObj,
        sum: num, //
        current: num, //
      };
      this.exportObj.excelData = [];
      this.exportCsv(this);
    },
    // 
    checklistExport() {
      this.btnDisabled = true;
      if (this.checklistData == '') {
        this.msg('');
        this.btnDisabled = false;
        return;
      }
      if (this.checklistData.length > 100000) {
        this.msg('');
        this.btnDisabled = false;
        return;
      }
      this.exportObj = {
        ...this.exportObj,
        sum: 1, //
        current: 1, //
        excelData: this.checklistData,
      };
      this.exportCsv(this);
      this.btnDisabled = false;
    },
    // 
    clearBtnDisabled() {
      this.btnDisabled = false;
    },
    msg(val, type = 'warning') {
      this.$message({
        showClose: true,
        message: val,
        type: type,
        duration: 3000,
      });
    },
      formatterFunc(arr, cellValue) {
        //formatterfilter
          const result = arr.filter(item => item.value === cellValue)
          return result.length > 0 ? result[0].label : '-'
      }
  },
};

/**
 * csvhead
 */
/**
 * @param paramIn  
 * @param totalRow   
 * @param type String daypage 
 * @param fileName csv 
 * @param url  url 
 * @param methodType   
 * @param startDateKeyStr   
 * @param endDateKeyStr     
 * @param isBillCode  
 * @param checklistData  
 * @param queryInternal    
 * @param pageSize ,5000 
 * 
 * exportBasicUtils 
 */
/**
 * 
 *   let paramObj = {
      paramIn: param,
      startDateKey: "startDate",
      endDateKey: "endDate",
      methodType: "post", 
      url: getDetailUrl,
      fileName: "",
      totalRow: this.currRow.totalTicket,
      queryInternal: 10,
      allEndDate: end,
      type: "day"
    };
    this.exportBasicUtils(paramObj);
 */
export const exportCsvUtils = {
  data() {
    return {
      // 
      btnDisabled: false, // 
      exportObj: {
        visible: false,
        sum: 1, //
        current: 0, //
        fileName: '',
        excelData: [],
      },
    };
  },
  methods: {
    exportBasicUtils(paramObj, pageSize = 5000) {
      //  10
      this.btnDisabled = true;
      setTimeout(() => {
        this.btnDisabled = false;
      }, 3000);
      const {
        totalRow,
        isBillCode,
        checklistData,
        fileName,
        type
      } = paramObj;
      // 
      this.exportObj.fileName = fileName;
      if (!totalRow) {
        this.msg('');
        return;
      }
      if (totalRow > 200000) {
        this.msg('20');
        return;
      }
      // 
      if (isBillCode || (checklistData && checklistData.length >= totalRow)) {
        if (checklistData.length > 0) {
          this.exportObj = {
            fileName: fileName,
            sum: 1, //
            current: 1, //
            excelData: checklistData,
            visible: true
          };
        } else {
          this.msg('');
        }
        return;
      }
      if (type == 'day') {
        this.exportObj = {
          excelData: [],
          fileName: fileName,
          sum: totalRow, //
          current: 1, //         
        };
        this.finished(paramObj, paramObj.paramIn, 0);
      } else {
        //    
        // 
        let num = Math.ceil(totalRow / pageSize);
        // 
        this.exportObj = {
          excelData: [],
          fileName: fileName,
          sum: num, //
          current: 0, //  
        };
        this.exportByPage(0, num, paramObj, pageSize);
      }
    },
    // 
    exportByPage(currentPage, num, paramObj, pageSize) {
      if (currentPage == num) {
        // 
        return;
      }
      const {
        methodType,
        url,
        paramIn
      } = paramObj;
      ++currentPage;
      let params = {
        ...paramIn,
        pageSize: pageSize,
        currentPage: currentPage,
      };
      axiosConfig(methodType, url, params, true, false)
        .then(result => {
          this.exportObj = {
            ...this.exportObj,
            sum: num,
            current: currentPage,
            excelData: result.data.list,
            visible: true,
          };
          return this.exportByPage(currentPage, num, paramObj, pageSize);
        })
        .catch(res => {
          this.msg('');
          this.exportObj = {
            ...this.exportObj,
            sum: num, // 
            current: num, //
            excelData: [],
            visible: true,
          };
        });
    },
    // v8
    callback(paramObj, param, currentTotal = 1) {
      const {
        methodType,
        url,
        totalRow
      } = paramObj;
      axiosConfig(methodType, url, param, true, false)
        .then(result => {
          // 
          currentTotal = currentTotal + result.data.list.length;
          if (result.data.currentPage >= result.data.totalPage) {
            this.finished(paramObj, param, currentTotal);
          } else {
            param.currentPage = result.data.currentPage;
            param.currentPage = param.currentPage + 1;
            this.callback(paramObj, param, currentTotal);
          }
          this.exportObj = {
            ...this.exportObj,
            sum: totalRow, // 
            current: currentTotal, //
            excelData: result.data.list,
            visible: true,
          };
        })
        .catch(res => {
          this.msg('');
          this.exportObj = {
            ...this.exportObj,
            sum: totalRow, // 
            current: totalRow, //
            excelData: [],
            visible: true,
          };
        });
    },
    //  0
    finished(paramObj, param, currentTotal = 0) {
      const {
        startDateKey,
        endDateKey,
        totalRow,
        fileName,
        allEndDate,
        queryInternal,
      } = paramObj;
      // 
      let interval = queryInternal ? queryInternal : 0;
      interval = interval > 0 ? interval - 1 : 0;
      let start = new Date(allEndDate).getTime();
      let end = new Date(param[endDateKey]).getTime();
      // 
      if (start == end && start > new Date(param[startDateKey]).getTime()) {
        param[startDateKey] = param[startDateKey];
      } else {
        param[startDateKey] = cloneDeep(
          this.addSeconds(param[endDateKey], 1)
        );
      }
      param[endDateKey] = cloneDeep(
        this.addSeconds(param[startDateKey], interval)
      );
      //
      if (
        new Date(allEndDate).getTime() <
        new Date(param[endDateKey]).getTime() ||
        totalRow <= 5000
      ) {
        param[endDateKey] = allEndDate;
      }
      // ,
      if (
        new Date(allEndDate).getTime() <
        new Date(param[startDateKey]).getTime()
      ) {
        // 
        setTimeout(() => {
          this.exportObj = {
            ...this.exportObj,
            fileName: fileName,
            sum: totalRow, // 
            current: totalRow, //
            excelData: [],
            visible: true,
          };
        }, 1500);
      } else {
        // 
        param.currentPage = 1;
        this.callback(paramObj, param, currentTotal);
      }
    },
    addSeconds(date, num = 0) {
      // 
      return moment(date).add(num, 'days').format('YYYY-MM-DD');
    },
    msg(val, type = 'warning') {
      this.$message({
        showClose: true,
        message: val,
        type: type,
        duration: 3000,
      });
    },
  },
};
